<template>
  <div class="team_page" id="team_page">
    <div class="title_part" id="title_part">
      <div class="title" id="title">{{ global.lang_pack.team }}</div>
      <div class="title_line" id="title_line"></div>
    </div>

    <div class="team_part" id="team_part">
      <img
        class="team_shape team_shape_1"
        id="team_shape_1"
        src="/static/team/team_shape_1.png"
      />
      <img
        class="team_img"
        id="team_img"
        src="/static/team/christy.png"
      />
      <div class="team_text_part" id="team_text_part">
        <div class="team_idea" id="team_idea">
          {{ global.lang_pack.christy_idea }}
        </div>
        <div
          class="team_text_line team_text_line_1"
          id="team_text_line"
        ></div>
        <div class="team_name" id="team_name">
          {{ global.lang_pack.christy }}
        </div>
        <div class="team_intro" id="team_intro">
          {{ global.lang_pack.christy_intro }}
        </div>
      </div>
    </div>

    <div class="team_part" id="team_part">
      <img
        class="team_shape team_shape_2"
        id="team_shape_2"
        src="/static/team/team_shape_2.png"
      />
      <img
        class="team_img"
        id="team_img"
        src="/static/team/max.png"
      />
      <div class="team_text_part" id="team_text_part">
        <div class="team_idea" id="team_idea">
          {{ global.lang_pack.max_idea }}
        </div>
        <div
          class="team_text_line team_text_line_2"
          id="team_text_line"
        ></div>
        <div class="team_name" id="team_name">
          {{ global.lang_pack.max }}
        </div>
        <div class="team_intro" id="team_intro">
          {{ global.lang_pack.max_intro }}
        </div>
      </div>
    </div>

    <div class="team_part" id="team_part">
      <img
        class="team_shape team_shape_3"
        id="team_shape_3"
        src="/static/team/team_shape_3.png"
      />
      <img
        class="team_img"
        id="team_img"
        src="/static/team/evan.png"
      />
      <div class="team_text_part" id="team_text_part">
        <div class="team_idea" id="team_idea">
          {{ global.lang_pack.evan_idea }}
        </div>
        <div
          class="team_text_line team_text_line_3"
          id="team_text_line"
        ></div>
        <div class="team_name" id="team_name">
          {{ global.lang_pack.evan }}
        </div>
        <div class="team_intro" id="team_intro">
          {{ global.lang_pack.evan_intro }}
        </div>
      </div>
    </div>
    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style scoped lang="css">
div {
  font-family: MyriadVariableConcept-Roman;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.team_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.title_part {
  margin-top: 100rem;
  margin-left: 300rem;
}

div.title {
  color: #251714;
  font-family: FuturaLT-ExtraBold;
  font-size: 100rem;
  text-transform: uppercase;
}

div.title_line {
  /* position: absolute; */
  background: #efcd24;
  width: 500rem;
  height: 8rem;
  left: 625rem;
}

div.team_part {
  /* background: #ffffff; */
  margin-top: 100rem;
  display: flex;
  flex-direction: row;
  padding-left: 100rem;
  padding-right: 300rem;
  align-items: center;
  /* height: 40rem; */
}

img.team_shape {
  display: inline-block;
  width: 80rem;
}

img.team_shape_1 {
  height: 160rem;
}

img.team_img {
  width: 500rem;
  padding-left: 100rem;
  padding-right: 100rem;
}

div.team_text_part {
  align-self: flex-start;
}

div.team_idea {
  font-size: 45rem;
  color: #251714;
  font-family: FuturaLT-Heavy;
  font-weight: bolder;
}

div.team_text_line {
  margin-top: 50rem;
  height: 2rem;
  /* width: 150rem; */
  background: #251714;
}

div.team_text_line_1 {
  width: 150rem;
}

div.team_text_line_2 {
  width: 100rem;
}

div.team_text_line_3 {
  width: 110rem;
}

div.team_name {
  font-family: FuturaLT-Heavy;
  font-size: 45rem;
  color: #251714;
}

div.team_intro {
  text-align: justify;
  text-justify: distribute-all-lines;
  letter-spacing: 0.5px;
  line-height: 2;
  /* width: 1000rem; */
  font-size: 18px;
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Team",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    this.start_why_3_check_anime();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {},
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    start_why_3_check_anime() {
      anime({
        // targets:"div."
      });
    },
  },
};
</script>



